<script>
import { flexible } from "../assets/js/flexible.js";
// import {web_socket} from '../assets/js/web_socket.js'
import BasicDataAdd from "../components/BasicDataAdd.vue";
import NewDistribution from "../components/NewDistribution.vue";
import NewTrendAdd from "../components/NewTrendAdd.vue";
import CumulativeTrend from "../components/CumulativeTrend.vue";
import China from "../components/China.vue";
import NewArea from "../components/NewArea.vue";
import InformationCarousel from "../components/InformationCarousel.vue";
export default {
  name: "ScreenPage",
  components: {
    BasicDataAdd,
    NewDistribution,
    NewTrendAdd,
    CumulativeTrend,
    China,
    NewArea,
    InformationCarousel
  },
  directives: {},
  data() {
    return {
      nowDate: "",
      basicData: [],
      basicDataAdd: [],
      websock: null,
      newTrend: []
    };
  },
  created() {
    this.initWebSocket();
  },
  mounted() {
    console.log(
      "本网站页面的基本样式以及布局借鉴了Pink老师开源课的素材，特地鸣谢（https://www.bilibili.com/video/BV1v7411R7mp?from=search&seid=15378873610382497455）"
    );
    console.log(
      "本网站所有数据都是基于百度疫情实时大数据报告网页所爬取的，实时可靠（https://voice.baidu.com/act/newpneumonia/newpneumonia）"
    );
    console.log(
      "本网站基于作者本人毕设所开发，耗时近一个月，90%的代码为原生代码，后续也将开源到本人的Gitee上"
    );
    this.currentTime();
    this.getBasicData();
    this.getBasicDataAdd();
  },
  methods: {
    currentTime() {
      setInterval(this.formatDate, 500);
    },
    formatDate() {
      let date = new Date();
      let year = date.getFullYear(); // 年
      let month = date.getMonth() + 1; // 月
      let day = date.getDate(); // 日
      let week = date.getDay(); // 星期
      let weekArr = [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六"
      ];
      let hour = date.getHours(); // 时
      hour = hour < 10 ? "0" + hour : hour; // 如果只有一位，则前面补零
      let minute = date.getMinutes(); // 分
      minute = minute < 10 ? "0" + minute : minute; // 如果只有一位，则前面补零
      let second = date.getSeconds(); // 秒
      second = second < 10 ? "0" + second : second; // 如果只有一位，则前面补零
      this.nowDate = `${year}年${month}月${day}日 ${hour}:${minute}:${second} ${weekArr[week]}`;
    },
    // 获取服务器数据
    async getBasicData() {
      const { data: ret } = await this.$http.post("basicData/select");
      this.basicData = ret.list[0];
    },
    // 获取服务器新增数据
    async getBasicDataAdd() {
      const { data: ret } = await this.$http.post("basicDataAdd/select");
      this.basicDataAdd = ret.list[0];
    },

    initWebSocket() {
      //初始化weosocket
      // const wsuri = "ws://127.0.0.1:8080/webSocket";
      const wsuri = "ws://lykcomeon.top:8082/webSocket";
      this.websock = new WebSocket(wsuri);
      this.websock.onmessage = this.websocketonmessage;
      this.websock.onopen = this.websocketonopen;
      this.websock.onerror = this.websocketonerror;
      this.websock.onclose = this.websocketclose;
    },
    websocketonopen() {
      //连接建立之后执行send方法发送数据
      console.log("建立连接");
      let actions = { name: "liyuankun" };
      this.websocketsend(JSON.stringify(actions));
    },
    websocketonerror() {
      //连接建立失败重连
      this.initWebSocket();
    },
    websocketonmessage(e) {
      //数据接收
      const redata = JSON.parse(e.data);
      if (redata.code === "1") {
        this.basicDataAdd = redata.list[0];
      }
      if (redata.code === "2") {
        this.basicData = redata.list[0];
      }
      if (redata.code === "3") {
        this.newTrend = redata.list;
      }
    },
    websocketsend(Data) {
      //数据发送
      this.websock.send(Data);
    },
    websocketclose() {
      //关闭
      console.log("断开连接");
    }
  },
  beforeDestroy() {
    if (this.formatDate) {
      clearInterval(this.formatDate); // 在Vue实例销毁前，清除时间定时器
    }
  },
  destroyed() {
    this.websock.close(); //离开路由之后断开websocket连接
  }
};
</script>

