<template>
  <div class="com-container">
    <div class="com-chart" ref="newArea_ref"></div>
  </div>
</template>

<script>
export default {
  name: "NewArea",
  components: {},
  directives: {},
  data() {
    return {
      allData: [],
      areaData: [],
      newNumberData: []
    };
  },
  mounted() {
    this.initChart(),
      this.getData(),
      window.addEventListener("resize", this.screenAdapter);
    // 在页面加载完成时，主动进行屏幕适配
    this.screenAdapter();
  },
  destroyed() {
    // clearInterval(this.timeId);
    // 组件销毁时，将监听器取消掉
    window.removeEventListener("resize", this.screenAdapter);
  },
  methods: {
    initChart() {
      this.chartInstance = this.$echarts.init(this.$refs.newArea_ref);
      const initOption = {
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          bottom: "0%",
          itemWidth: 10,
          itemHeight: 10,
          textStyle: {
            color: "rgba(255,255,255,.5)",
            fontSize: 10
          }
        },
        series: [
          {
            name: "地区分布",
            type: "pie",
            radius: ["10%", "60%"],
            center: ["50%", "45%"],
            roseType: "radius",
            // 图形的文字标签
            label: {
              normal: {
                show: true,
                textStyle: {
                  fontSize: 12 //文字的字体大小
                },
                formatter: "{b}: {c}" + "例"
              }
            },
            // 链接图形和文字的线条
            labelLine: {
              // length 链接图形的线条
              length: 6,
              // length2 链接文字的线条
              length2: 8
            },
            data: this.allData
          }
        ]
      };
      this.chartInstance.setOption(initOption);
    },
    // 获取服务器数据
    async getData() {
      const { data: ret } = await this.$http.get("areaData/select");
      // this.allData = ret.list
      ret.list.map(item => {
        if (parseInt(item.newNumber) !== 0) {
          this.areaData.push(item);
        }
      });
      this.allData = this.areaData;
      this.allData.sort((a, b) => {
        return b.newNumber - a.newNumber; //从小到大排序

      });
      // 每5个元素显示一页
      // this.totalPage = this.allData.length % 9 === 0 ? this.allData.length / 9 : this.allData.length / 9 + 1
      this.updateChart();
      //启动定时器
      // this.startInterval();
    },
    updateChart() {
      const colorArr = [
        "#0090ff",
        "#06d3c4",
        "#ffbc32",
        "#2ccc44",
        "#ff3976",
        "#6173d6",
        "#914ce5",
        "#42b1cc",
        "#ff55ac",
        "#0090ff",
        "#06d3c4",
        "#ffbc32",
        "#2ccc44",
        "#ff3976",
        "#6173d6",
        "#914ce5",
        "#42b1cc",
        "#ff55ac",
        "#0090ff",
        "#06d3c4",
        "#ffbc32",
        "#2ccc44",
        "#ff3976",
        "#6173d6",
        "#914ce5",
        "#42b1cc",
        "#ff55ac",
        "#0090ff",
        "#06d3c4",
        "#ffbc32",
        "#2ccc44",
        "#ff3976",
        "#6173d6",
        "#914ce5",
        "#42b1cc",
        "#ff55ac",
        "#0090ff",
        "#06d3c4",
        "#ffbc32",
        "#2ccc44",
        "#ff3976",
        "#6173d6",
        "#914ce5",
        "#42b1cc",
        "#ff55ac",
        "#0090ff",
        "#06d3c4",
        "#ffbc32",
        "#2ccc44",
        "#ff3976",
        "#6173d6",
        "#914ce5",
        "#42b1cc",
        "#ff55ac",
        "#0090ff",
        "#06d3c4",
        "#ffbc32",
        "#2ccc44",
        "#ff3976",
        "#6173d6",
        "#914ce5",
        "#42b1cc",
        "#ff55ac",
        "#0090ff",
        "#06d3c4",
        "#ffbc32",
        "#2ccc44",
        "#ff3976",
        "#6173d6",
        "#914ce5",
        "#42b1cc",
        "#ff55ac",
        "#0090ff",
        "#06d3c4",
        "#ffbc32",
        "#2ccc44",
        "#ff3976",
        "#6173d6",
        "#914ce5",
        "#42b1cc",
        "#ff55ac",
        "#0090ff",
        "#06d3c4",
        "#ffbc32",
        "#2ccc44",
        "#ff3976",
        "#6173d6",
        "#914ce5",
        "#42b1cc",
        "#ff55ac",
        "#0090ff",
        "#06d3c4",
        "#ffbc32",
        "#2ccc44",
        "#ff3976",
        "#6173d6",
        "#914ce5",
        "#42b1cc",
        "#ff55ac"
      ];
      const newNumberResult = this.allData.map(item => ({
        name: item.area,
        value: item.newNumber
      }));
      this.newNumberData = newNumberResult;
      const updateOption = {
        color: colorArr,
        series: [
          {
            data: this.newNumberData
          }
        ]
      };
      this.chartInstance.setOption(updateOption);
    },
    //当浏览器的大小发生变化时，适配浏览器
    screenAdapter() {
      const titleFontSize = (this.$refs.newArea_ref.offsetWidth / 100) * 3.6;
      this.chartInstance.resize();
    }
  }
};
</script>

