<template>
  <div class="com-container">
    <div class="com-chart" ref="basicDataAdd_ref"></div>
  </div>
</template>

<script>
export default {
  name: "BasicDataAdd",
  components: {},
  directives: {},
  data() {
    return {
      chartInstance: null,
      areaData:[],
      allData: [
        { city: "河北", value: 5533 },
        { city: "北京", value: 2333 },
        { city: "黑龙江", value: 1000 },
        { city: "吉林", value: 980 },
        { city: "浙江", value: 789 },
        { city: "福建", value: 543 },
        { city: "广东", value: 123 },
        { city: "上海", value: 99 },
        { city: "香港", value: 78 },
        { city: "四川", value: 20 },
        { city: "陕西", value: 90 },
        { city: "天津", value: 900 },
        { city: "江苏", value: 478 },
        { city: "安徽", value: 433 },
        { city: "西藏", value: 1 },
        { city: "湖南", value: 167 },
        { city: "海南", value: 10 },
        { city: "新疆", value: 55 },
        { city: "甘肃", value: 67 },
        { city: "河南", value: 789 },
        { city: "湖北", value: 1098 },
        { city: "云南", value: 234 },
        { city: "贵州", value: 22 }
      ], //服务器返回的数据
      currentPage: 1, //当前显示的页数
      totalPage: 0, //一共有多少页
      timeId: null, //定时器标识
      startValue: 0,
      endValue: 9
    };
  },
  mounted() {
    this.initChart(),
      this.getData(),
      window.addEventListener("resize", this.screenAdapter);
    // 在页面加载完成时，主动进行屏幕适配
    this.screenAdapter();
  },
  destroyed() {
    clearInterval(this.timeId);
    // 组件销毁时，将监听器取消掉
    window.removeEventListener("resize", this.screenAdapter);
  },
  methods: {
    // 初始化echartInstance对象
    initChart() {
      this.chartInstance = this.$echarts.init(this.$refs.basicDataAdd_ref);
      // 对图标初始化配置控制
      const initOption = {
        color: ["#2f89cf"],
        // title: {
        //   text: "现有分布",
        //   textStyle: {
        //     fontSize: 66
        //     // color: '#fff'
        //   },
        //   left: 630,
        //   top: 20
        // },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          left: "0%",
          top: "28px",
          right: "0%",
          bottom: "4%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          axisTick: {
            alignWithLabel: true
          },
          // 修改刻度标签 相关样式
          axisLabel: {
            color: "rgba(255,255,255,.6)",
            // fontSize: "9",
            fontSize: 11,
            interval: 0
          },
          // 不显示x坐标轴的样式
          axisLine: {
            show: false
          }
        },
        yAxis: {
          type: "value",
          name: "单位 例",
          nameLocation: "end",
          nameTextStyle: {
            color: "rgba(255,255,255,.6)",
            fontSize: 11
          },
          // 修改刻度标签 相关样式
          axisLabel: {
            color: "rgba(255,255,255,.6)",
            fontSize: 11
          },
          // y轴的线条改为了 2像素
          axisLine: {
            lineStyle: {
              color: "rgba(255,255,255,.1)",
              width: 2
            }
          },
          // y轴分割线的颜色
          splitLine: {
            lineStyle: {
              color: "rgba(255,255,255,.1)"
            }
          }
        },
        series: [
          {
            name: "现有确诊",
            type: "bar",
            barWidth: "35%",
            label: {
              show: true,
              position: "top",
              color: "rgba(255,255,255,.6)",
              fontSize: 11
            }
          }
        ]
      };
      this.chartInstance.setOption(initOption);
      // 对图标对象进行鼠标事件的监听
      this.chartInstance.on("mouseover", () => {
        clearInterval(this.timeId);
      });
      this.chartInstance.on("mouseout", () => {
        this.startInterval();
      });
    },
    // 获取服务器数据
    async getData() {
      const {data: ret} = await this.$http.get('areaData/select')
      // this.allData = ret.list
      ret.list.map(item => {
        if (parseInt(item.existingNumber) !== 0) {
          this.areaData.push(item);
        } 
      });
      this.allData = this.areaData
      this.allData.sort((a, b) => {
        return b.existingNumber - a.existingNumber; //从小到大排序
      });
      // 每5个元素显示一页
      // this.totalPage = this.allData.length % 9 === 0 ? this.allData.length / 9 : this.allData.length / 9 + 1
      this.updateChart();
      //启动定时器
      this.startInterval();
    },
    // 更新图表
    updateChart() {
      const colorArr = [
        ["#8B0000", "#B22222"],
        ["#FF4500", "#FF7F50"],
        ["#D2691E","#F4A460"],
        ["#808000","#BDB76B"],
        ["#228B22","#00FF00"]
      ];
      // const start = (this.currentPage - 1) * 9
      // const end = this.currentPage * 9
      // const showData = this.allData.slice(start,end)
      const basicDataAddCity = this.allData.map(item => {
        return item.area;
      });
      const basicDataAddValues = this.allData.map(item => {
        return item.existingNumber;
      });
      const updateOption = {
        xAxis: {
          data: basicDataAddCity
        },
        dataZoom: {
          show: false,
          startValue: this.startValue,
          endValue: this.endValue
        },
        series: [
          {
            data: basicDataAddValues,
            itemStyle: {
              color: arg => {
                let targetColorArr = null;
                if (arg.value > 50) {
                  targetColorArr = colorArr[0];
                } else if (arg.value > 20) {
                  targetColorArr = colorArr[1];
                } else if (arg.value > 5){
                  targetColorArr = colorArr[2];
                }else {
                  targetColorArr = colorArr[3];
                }
                return new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: targetColorArr[0]
                  },
                  {
                    offset: 1,
                    color: targetColorArr[1]
                  }
                ]);
              }
            }
          }
        ]
      };
      this.chartInstance.setOption(updateOption);
    },
    startInterval() {
      // if (this.timeId) {
      //   clearInterval(this.timeId);
      // }
      // this.timeId = setInterval(() => {
      //   this.currentPage++;
      //   if (this.currentPage > this.totalPage) {
      //     this.currentPage = 1;
      //   }
      //   this.updateChart();
      // }, 3000);
      if (this.timeId) {
        clearInterval(this.timeId);
      }
      this.timeId = setInterval(() => {
        this.startValue++;
        this.endValue++;
        if (this.endValue > this.allData.length - 1) {
          this.startValue = 0;
          this.endValue = 9;
        }
        this.updateChart();
      }, 2000);
    },
    //当浏览器的大小发生变化时，适配浏览器
    screenAdapter() {
      const titleFontSize =
        (this.$refs.basicDataAdd_ref.offsetWidth / 100) * 3.6;
      const adapterOption = {
        title: {
          textStyle: {
            fontSize: titleFontSize
            // color: '#fff'
          }
        },
        series: [
          {
            barWidth: titleFontSize,
            itemStyle: {
              // 修改柱子圆角
              barBorderRadius: [5, 5, 0, 0]
              // barBorderRadius: [titleFontSize / 2, titleFontSize / 2, 0, 0]
            }
          }
        ]
      };
      this.chartInstance.setOption(adapterOption);
      this.chartInstance.resize();
    }
  }
};
</script>

<style lang="less" scoped>
</style>