<template>
  <div class="com-container">
    <div class="head">
      <span @click="update()" :class="[isActive?'change':'changs']">现有确诊</span>
      <span> / </span>
      <span @click="updateChart()" :class="[isActive2?'change':'changs']">累计确诊</span>
      </div>
    <div class="com-chart" ref="china_ref"></div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "China",
  components: {},
  directives: {},
  data() {
    return {
      isActive: false,
      isActive2: true,
      chartInstance: null,
      allData: [],
      existingData: [],
      cumulativeData: [],
      img: "image://data:image/png;base64,"
    };
  },
  mounted() {
    this.initChart(),
    this.getData(),
    window.addEventListener("resize", this.screenAdapter);
    // 在页面加载完成时，主动进行屏幕适配
    this.screenAdapter();
  },
  destroyed() {
    // 组件销毁时，将监听器取消掉
    window.removeEventListener("resize", this.screenAdapter);
  },
  methods: {
    // 获取服务器数据
    async getData() {
      const {data: ret} = await this.$http.get('areaData/select')
      this.allData = ret.list
      const existingResult = this.allData.map(item => ({
        name: item.area,
        value: item.existingNumber
      }));
      const cumulativeResult = this.allData.map(item => ({
        name: item.area,
        value: item.cumulativeNumber
      }));
      this.existingData = existingResult
      this.cumulativeData = cumulativeResult
      this.update()
    },
    async initChart() {
      this.chartInstance = this.$echarts.init(this.$refs.china_ref);
      // const ret = await axios.get('http://localhost:8081/static/map/china.json');
      const ret = await axios.get('http://lykcomeon.top/static/map/china.json');
      this.$echarts.registerMap('china',ret.data)
      
      // 对图标初始化配置控制
      const initOption = {
        title: {
          top: 20,
          text: "现有确诊病例数，排除治愈、死亡",
          x: "center",
          textStyle: {
            color: "#ccc",
            fontSize: 12
          }
        },
        tooltip: {
          trigger: "item",
          formatter: function(e, t, n) {
            return  "地区："  + e.name + "<br />" + "确诊：" + e.value;
          },
          textStyle: {
            fontSize: 10
          }
        },
        visualMap: {
          min: 0,
          max: 1000,
          left: 10,
          bottom: 60,
          showLabel: true,
          textStyle: {
            color: "#ccc",
            fontSize: 10
          },
          itemWidth: 10,
          itemHeight: 10,
          pieces: [
            {
              gte: 10000,
              label: "≥ 10000",
              color: "#B80909",
              symbol: "roundRect"
            },
            {
              gte: 1000,
              lte: 9999,
              label: "1000 - 9999",
              color: "#E64546",
              symbol: "roundRect"
            },
            {
              gte: 100,
              lt: 1000,
              label: "100 - 999",
              color: "#F57567",
              symbol: "roundRect"
            },
            {
              gt: 9,
              lt: 100,
              label: "10 - 99",
              color: "#FF9985",
              symbol: "roundRect"
            },
            {
              gt: 0,
              lt: 10,
              label: "1 - 9",
              color: "#FFE5DB",
              symbol: "roundRect"
            },
            {
              value: 0,
              color: "#fff",
              symbol: "roundRect",
            }
          ],
          show: true
        },
        geo: {
          type: 'map',
          map: "china",
          roam: true,
          // scaleLimit: {
          //     min: 0.5,
          //     max: 3
          // },
          zoom: 1.2,
          // top: 120,
          label: {
            normal: {
              show: true,
              fontSize: 10,
              color: "#fff"
            },
          },
          itemStyle: {
            normal: {
              // 地图省份的背景颜色
              areaColor: "rgba(20, 41, 87,0.6)",
              // borderColor: "#195BB9",
              borderColor: "#D3D3D3",
              borderWidth: 0.5,
              opacity: 0.7
            },
            emphasis: {
              areaColor: "#D3D3D3",
              shadowOffsetX: 0,
              shadowOffsetY: 0,
              borderWidth: 0
            }
          }
        },
        series: [
          {
            type: "map",
            geoIndex: 0,
            // data: this.existingData
          }
        ]
      };
      this.chartInstance.setOption(initOption);
      // 对图标对象进行鼠标事件的监听
      // this.chartInstance.on("mouseover", () => {
      //   clearInterval(this.timeId);
      // });
      // this.chartInstance.on("mouseout", () => {
      //   this.startInterval();
      // });
    },
    updateChart() {
      this.isActive = true;
      this.isActive2 = false;
      const updataOption = {
        title: {
          text: "累计确诊病例数，包含治愈、死亡",
          textStyle: {
            color: "#ccc",
            fontSize: 12
          }
        },
        series: [
          {
            type: "map",
            geoIndex: 0,
            data: this.cumulativeData
          }
        ]
      };
      this.chartInstance.setOption(updataOption);
    },
    update() {
      this.isActive = false;
      this.isActive2 = true;
      const updataOption = {
        title: {
          text: "现有确诊病例数，排除治愈、死亡",
          textStyle: {
            color: "#ccc",
            fontSize: 12
          }
        },
        series: [
          {
            type: "map",
            geoIndex: 0,
            data: this.existingData
          }
        ]
      };
      this.chartInstance.setOption(updataOption);
    },
    //当浏览器的大小发生变化时，适配浏览器
    screenAdapter() {
      const titleFontSize =
        (this.$refs.china_ref.offsetWidth / 100) * 3.6;

      const adapterOption = {
        title: {
          // textStyle: {
          //   fontSize: titleFontSize
          //   // color: '#fff'
          // }
        },
        legend: {
          itemWidth: titleFontSize / 2,
          itemHeight: titleFontSize / 2,
          textStyle: {
            fontSize: titleFontSize / 2
          }
        }
      };
      this.chartInstance.setOption(adapterOption);
      this.chartInstance.resize();
    }
  }
};
</script>

<style lang="less" scoped>

.head{
  display: block;
  text-align: center;
}

span{
  color: #ccc;
  font-size: 12px;
}

.change {
  color: #ccc;
  font-size: 12px;
  cursor: pointer;
}

.changs {
  color: #FFB6C1;
  font-weight: bolder;
  font-size: 14px;
  cursor: pointer;
}

</style>