<template>
  <div class="com-container">
    <div class="com-chart" ref="basicDataAdd_ref"></div>
  </div>
</template>

<script>
export default {
  name: "BasicDataAdd",
  components: {},
  directives: {},
  data() {
    return {
      chartInstance: null,
      allData: [
        { city: "河北", localValue: 5533, abroadValue: 0 },
        { city: "北京", localValue: 2333, abroadValue: 123 },
        { city: "黑龙江", localValue: 1000, abroadValue: 1 },
        { city: "吉林", localValue: 980, abroadValue: 99 },
        { city: "浙江", localValue: 789, abroadValue: 33 },
        { city: "福建", localValue: 543, abroadValue: 90 },
        { city: "广东", localValue: 123, abroadValue: 0 },
        { city: "上海", localValue: 99, abroadValue: 0 },
        { city: "香港", localValue: 78, abroadValue: 23 },
        { city: "四川", localValue: 20, abroadValue: 0},
        { city: "陕西", localValue: 90, abroadValue: 12 },
        { city: "天津", localValue: 900, abroadValue: 23 },
        { city: "江苏", localValue: 478, abroadValue: 89 },
        { city: "安徽", localValue: 433, abroadValue: 90 },
        { city: "西藏", localValue: 1, abroadValue: 0 },
        { city: "湖南", localValue: 167, abroadValue: 32 },
        { city: "海南", localValue: 10, abroadValue: 312 },
        { city: "新疆", localValue: 55, abroadValue: 32 },
        { city: "甘肃", localValue: 67, abroadValue: 56 },
        { city: "河南", localValue: 789, abroadValue: 78 },
        { city: "湖北", localValue: 1098, abroadValue: 89 },
        { city: "云南", localValue: 234, abroadValue: 32 },
        { city: "贵州", localValue: 22, abroadValue: 12 }
      ], //服务器返回的数据
      areaData:[],
      currentPage: 1, //当前显示的页数
      totalPage: 0, //一共有多少页
      timeId: null, //定时器标识
      startValue: 0,
      endValue: 9
    };
  },
  mounted() {
    this.initChart(),
    this.getData(),
    window.addEventListener("resize", this.screenAdapter);
    // 在页面加载完成时，主动进行屏幕适配
    this.screenAdapter();
  },
  destroyed() {
    clearInterval(this.timeId);
    // 组件销毁时，将监听器取消掉
    window.removeEventListener("resize", this.screenAdapter);
  },
  methods: {
    // 初始化echartInstance对象
    initChart() {
      this.chartInstance = this.$echarts.init(this.$refs.basicDataAdd_ref);
      // 对图标初始化配置控制
      const initOption = {
        color: ["#2f89cf"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          left: "0%",
          top: "28px",
          right: "0%",
          bottom: "4%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          axisTick: {
            alignWithLabel: true
          },
          // 修改刻度标签 相关样式
          axisLabel: {
            color: "rgba(255,255,255,.6)",
            fontSize: 11,
            interval: 0
          },
          // 不显示x坐标轴的样式
          axisLine: {
            show: false
          }
        },
        yAxis: {
          type: "value",
          name: "单位 例",
          nameLocation: "end",
          nameTextStyle: {
            color: "rgba(255,255,255,.6)",
            fontSize: 11
          },
          // 修改刻度标签 相关样式
          axisLabel: {
            color: "rgba(255,255,255,.6)",
            fontSize: 11
          },
          // y轴的线条改为了 2像素
          axisLine: {
            lineStyle: {
              color: "rgba(255,255,255,.1)",
              width: 2
            }
          },
          // y轴分割线的颜色
          splitLine: {
            lineStyle: {
              color: "rgba(255,255,255,.1)"
            }
          }
        },
        series: [
          {
            name: "累计确诊",
            type: "bar",
            barWidth: "35%",
            stack: '总量',
            label: {
              show: true,
              position: "top",
              color: "rgba(255,255,255,.6)",
              fontSize: 11
            }
          },
        ]
      };
      this.chartInstance.setOption(initOption);
      // 对图标对象进行鼠标事件的监听
      this.chartInstance.on("mouseover", () => {
        clearInterval(this.timeId);
      });
      this.chartInstance.on("mouseout", () => {
        this.startInterval();
      });
    },
    // 获取服务器数据
    async getData() {
      const {data: ret} = await this.$http.get('areaData/select')
      this.allData = ret.list
      this.allData.sort((a, b) => {
        return b.cumulativeNumber - a.cumulativeNumber; //从小到大排序
      });
      // 每5个元素显示一页
      this.totalPage = this.allData.length % 9 === 0 ? this.allData.length / 9 : this.allData.length / 9 + 1
      this.updateChart();
      //启动定时器
      this.startInterval();
    },
    // 更新图表
    updateChart() {
      const colorArr = [
        ["#8B0000", "#B22222"],
        ["#FF4500", "#FF7F50"],
        ["#D2691E","#F4A460"],
        ["#808000","#BDB76B"],
        ["#228B22","#00FF00"]
      ];
      const start = (this.currentPage - 1) * 9
      const end = this.currentPage * 9
      const showData = this.allData.slice(start,end)
      const basicDataAddCity = showData.map(item => {
        return item.area;
      });
      const basicDataAddLocalValues = showData.map(item => {
        return item.cumulativeNumber;
      });
      const updateOption = {
        xAxis: {
          data: basicDataAddCity
        },
        dataZoom: {
          show: false,
          startValue: this.startValue,
          endValue: this.endValue
        },
        series: [
          {
            data: basicDataAddLocalValues,
            itemStyle: {
              color: "#c05c3d",
              color: arg => {
                let targetColorArr = null;
                if (arg.value > 2000) {
                  targetColorArr = colorArr[0];
                } else if (arg.value > 1000) {
                  targetColorArr = colorArr[1];
                } else if (arg.value > 500) {
                  targetColorArr = colorArr[2];
                } else if (arg.value > 100) {
                  targetColorArr = colorArr[3];
                }  else {
                  targetColorArr = colorArr[4];
                }
                return new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: targetColorArr[0]
                  },
                  {
                    offset: 1,
                    color: targetColorArr[1]
                  }
                ]);
              }
            }
          },
        ]
      };
      this.chartInstance.setOption(updateOption);
    },
    startInterval() {
      if (this.timeId) {
        clearInterval(this.timeId);
      }
      this.timeId = setInterval(() => {
        this.currentPage++;
        if (this.currentPage > this.totalPage) {
          this.currentPage = 1;
        }
        this.updateChart();
      }, 3000);
      // if (this.timeId) {
      //   clearInterval(this.timeId);
      // }
      // this.timeId = setInterval(() => {
      //   this.startValue++;
      //   this.endValue++;
      //   if (this.endValue > this.allData.length - 1) {
      //     this.startValue = 0;
      //     this.endValue = 9;
      //   }
      //   this.updateChart();
      // }, 2000);
    },
    //当浏览器的大小发生变化时，适配浏览器
    screenAdapter() {
      const titleFontSize =
        (this.$refs.basicDataAdd_ref.offsetWidth / 100) * 3.6;
      const adapterOption = {
        title: {
          textStyle: {
            fontSize: titleFontSize
            // color: '#fff'
          }
        },
        series: [
          {
            barWidth: titleFontSize,
            itemStyle: {
              // 修改柱子圆角
              barBorderRadius: [5, 5, 0, 0],
              // barBorderRadius: [titleFontSize / 2, titleFontSize / 2, 0, 0]
            }
          },
          // {
          //   barWidth: titleFontSize,
          //   itemStyle: {
          //     // 修改柱子圆角
          //     // barBorderRadius: [5, 5, 0, 0]
          //     // barBorderRadius: [titleFontSize / 2, titleFontSize / 2, 0, 0]
          //   }
          // }
        ]
      };
      this.chartInstance.setOption(adapterOption);
      this.chartInstance.resize();
    }
  }
};
</script>

<style lang="less" scoped>
</style>