<template>
  <div class="com-container">
    <div class="com-chart" ref="newTrendAdd_ref"></div>
  </div>
</template>

<script>
export default {
  name: "NewTrendAdd",
  components: {},
  directives: {},
  data() {
    return {
      chartInstance: null,
      allData: [], //服务器返回的数据
      currentPage: 1, //当前显示的页数
      totalPage: 0, //一共有多少页
      timeId: null, //定时器标识
      // timer: null,
      startValue: 0,
      endValue: 30,
      showData: false,
      newTrendData: []
    };
  },
  props: {
    //props列表
    newTrend: Array //定义参数类型
  },
  watch: {
      allData(val) {
        // this.allData = this.newTrend;
      }
    },
  mounted() {
    this.initChart(),
      this.getData(),
      window.addEventListener("resize", this.screenAdapter);
    // 在页面加载完成时，主动进行屏幕适配
    this.screenAdapter();
    this.$refs.newTrendAdd_ref.addEventListener("dblclick", this.clickOther);
    // this.timer = setInterval(this.getData(), 180000);
  },
  destroyed() {
    clearInterval(this.timeId);
    // clearInterval(this.timer);
    // 组件销毁时，将监听器取消掉
    window.removeEventListener("resize", this.screenAdapter);
    this.$refs.newTrendAdd_ref.removeEventListener("dblclick", this.clickOther);
  },
  methods: {
    // 初始化echartInstance对象
    initChart() {
      this.chartInstance = this.$echarts.init(this.$refs.newTrendAdd_ref);
      // 对图标初始化配置控制
      const initOption = {
        color: ["#ed3f35", "#ec9217", "#00f2f1", "#4d5054"],
        tooltip: {
          trigger: "axis"
        },
        legend: {
          itemGap: 12,
          itemHeight: 10,
          itemWidth: 16,
          // 如果series 对象有name 值，则 legend可以不用写data
          // 修改图例组件 文字颜色
          textStyle: {
            fontSize: 11,
            color: "#4c9bfd"
          },
          // 这个10% 必须加引号
          right: "0%"
        },
        grid: {
          left: "0%",
          top: "26px",
          right: "3%",
          bottom: "4%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          axisTick: {
            show: false // 去除刻度线
          },
          axisLabel: {
            color: "rgba(255,255,255,.6)", // 文本颜色
            fontSize: 11
          },
          axisLine: {
            show: false // 去除轴线
          }
        },
        yAxis: {
          type: "value",
          axisTick: {
            show: false // 去除刻度线
          },
          axisLabel: {
            color: "rgba(255,255,255,.6)", // 文本颜色
            fontSize: 11
          },
          axisLine: {
            show: false // 去除轴线
          },
          splitLine: {
            lineStyle: {
              color: "#012f4a" // 分割线颜色
            }
          }
        },
        series: [
          {
            name: "新增确诊",
            type: "line",
            // true 可以让我们的折线显示带有弧度
            smooth: true,
            showSymbol: false
          },
          {
            name: "新增疑似",
            type: "line",
            showSymbol: false,
            // true 可以让我们的折线显示带有弧度
            smooth: true
          },
          {
            name: "新增治愈",
            type: "line",
            showSymbol: false,
            smooth: true
          },
          {
            name: "新增死亡",
            type: "line",
            showSymbol: false,
            smooth: true
          }
        ]
      };
      this.chartInstance.setOption(initOption);
      // 对图标对象进行鼠标事件的监听
      this.chartInstance.on("mouseover", () => {
        clearInterval(this.timeId);
      });
      this.chartInstance.on("mouseout", () => {
        if (!this.showData) {
          this.startInterval();
        }
      });
    },
    clickOther() {
      this.showData = !this.showData;
      if (this.showData) {
        clearInterval(this.timeId);
        this.showDataZoom();
        this.bottomGrid();
      }
      if (!this.showData) {
        this.startInterval();
        this.topGrid();
        this.closeDataZoom();
      }
    },
    bottomGrid() {
      const bottomGrid = {
        grid: {
          bottom: "10%"
        }
      };
      this.chartInstance.setOption(bottomGrid);
    },
    topGrid() {
      const topGrid = {
        grid: {
          bottom: "4%"
        }
      };
      this.chartInstance.setOption(topGrid);
    },
    closeDataZoom() {
      const closeDataZoom = {
        dataZoom: {
          show: this.showData,
          startValue: this.startValue,
          endValue: this.endValue
        }
      };
      this.chartInstance.setOption(closeDataZoom);
    },
    showDataZoom() {
      const showOption = {
        dataZoom: [
          {
            type: "slider",
            show: this.showData,
            left: "20%",
            top: "90%",
            right: "20%",
            start: 0,
            end: 100,
            height: 15, //组件高度
            width: "60%",
            handleIcon:
              "path://M306.1,413c0,2.2-1.8,4-4,4h-59.8c-2.2,0-4-1.8-4-4V200.8c0-2.2,1.8-4,4-4h59.8c2.2,0,4,1.8,4,4V413z",
            handleSize: "110%",
            handleStyle: {
              color: "rgba(47,137,207,0.7)"
            },
            backgroundColor: "rgba(47,137,207,0.1)",
            dataBackground: {
              lineStyle: {
                color: "rgba(47,137,207,0.7)"
              },
              areaStyle: {
                color: "rgba(47,137,207,0.7)"
              }
            },
            borderColor: "#012f4a",
            showDetail: true,
            showDataShadow: true,
            textStyle: {
              color: "rgba(255,255,255,.6)",
              fontSize: 11
            }
          }
          // {
          //   type: "inside",
          //   height: 15 //组件高度
          // }
        ]
      };
      this.chartInstance.setOption(showOption);
    },
    // 获取服务器数据
    async getData() {
      const { data: ret } = await this.$http.post("newTrend/select");
      this.allData = ret.list;
      // this.allData = ret
      // this.allData.sort((a, b) => {
      //   return b.value - a.value; //从小到大排序
      // });
      // 每5个元素显示一页
      // this.totalPage = this.allData.length % 9 === 0 ? this.allData.length / 9 : this.allData.length / 9 + 1
      this.updateChart();
      //启动定时器
      this.startInterval();
    },
    // 更新图表
    updateChart() {
      const colorArr = [
        ["#0BA82C", "#4FF778"],
        ["#2E72BF", "#23E5E5"],
        ["#5052EE", "#AB6EE5"]
      ];
      // const start = (this.currentPage - 1) * 9
      // const end = this.currentPage * 9
      // const showData = this.allData.slice(start,end)
      const newTrendAddMonth = this.allData.map(item => {
        return item.date;
      });
      const newTrendAddConfirmedAdd = this.allData.map(item => {
        return item.newlyDiagnosed;
      });
      const newTrendAddSuspectAdd = this.allData.map(item => {
        return item.addSuspect;
      });
      const newTrendAddCureAdd = this.allData.map(item => {
        return item.addCure;
      });
      const newTrendAddDeaths = this.allData.map(item => {
        return item.newDeath;
      });
      const updateOption = {
        xAxis: {
          data: newTrendAddMonth
        },
        dataZoom: {
          show: this.showData,
          startValue: this.startValue,
          endValue: this.endValue
        },
        series: [
          {
            data: newTrendAddConfirmedAdd
          },
          {
            data: newTrendAddSuspectAdd
          },
          {
            data: newTrendAddCureAdd
          },
          {
            data: newTrendAddDeaths
          }
        ]
      };
      this.chartInstance.setOption(updateOption);
    },
    startInterval() {
      // if (this.timeId) {
      //   clearInterval(this.timeId);
      // }
      // this.timeId = setInterval(() => {
      //   this.currentPage++;
      //   if (this.currentPage > this.totalPage) {
      //     this.currentPage = 1;
      //   }
      //   this.updateChart();
      // }, 3000);
      if (this.timeId) {
        clearInterval(this.timeId);
      }
      this.timeId = setInterval(() => {
        this.startValue = this.startValue + 7;
        this.endValue = this.endValue + 7;
        if (this.endValue > this.allData.length - 1) {
          this.startValue = 0;
          this.endValue = 30;
        }
        this.updateChart();
      }, 1000);
    },
    //当浏览器的大小发生变化时，适配浏览器
    screenAdapter() {
      const titleFontSize =
        (this.$refs.newTrendAdd_ref.offsetWidth / 100) * 3.6;
      const adapterOption = {
        title: {
          textStyle: {
            fontSize: titleFontSize
            // color: '#fff'
          }
        },
        series: [
          {
            barWidth: titleFontSize,
            itemStyle: {
              // 修改柱子圆角
              // barBorderRadius: [5, 5, 0, 0]
              // barBorderRadius: [titleFontSize / 2, titleFontSize / 2, 0, 0]
            }
          },
          {
            barWidth: titleFontSize,
            itemStyle: {
              // 修改柱子圆角
              // barBorderRadius: [5, 5, 0, 0]
              // barBorderRadius: [titleFontSize / 2, titleFontSize / 2, 0, 0]
            }
          }
        ]
      };
      this.chartInstance.setOption(adapterOption);
      this.chartInstance.resize();
    }
  }
};
</script>

<style lang="less" scoped>
</style>